
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import {orderInfo, backOrder, QQ} from '../config'
export default {
  name: 'orderDetail',
  head: {
    title: {
      inner: '订单详情'
    }
  },
  components: {
    Group,
    Cell,
    Flexbox,
    FlexboxItem,
    XButton,
    Divider
  },
  data () {
    return {
      id: 0,
      order: null,
      qq: QQ,
      hasInsurance: false,
      hasForce: false,
      form: {
        userId: '',
        orderId: ''
      }
    }
  },
  created () {
    this.form.orderId = this.$route.params.orderId
    this.form.userId = this.$route.params.userId
    this.getDetail()
  },
  methods: {
    handleBack () {
      this.$http({
        method: 'jsonp',
        url: backOrder,
        jsonp: 'callback',
        jsonpCallback: 'json',
        params: this.form
      })
      .then(res => {
        if (res.body.status) {
          this.$vux.toast.show({
            type: 'text',
            width: '22em',
            position: 'bottom',
            text: '成功撤销订单，欢迎再次为您服务！',
            time: '1000'
          })
        }
        setTimeout(() => {
          this.$router.replace('/order/-100/' + this.form.userId)
        }, 1000)
      })
    },
    changeStatus (num) {
      switch (num) {
        case 0:
          return {
            icon: 'icon-tishi',
            status: '待报价',
            text: '报价结果将在<span style="color:#EB3D00;padding:0 5px;">1</span>个工作日之内反馈'
          }
        case 1:
          return {
            icon: 'icon-faild',
            status: '已撤销',
            text: '您已主动撤销订单，给您带来不便敬请谅解'
          }
        case 2:
          return {
            icon: 'icon-time',
            status: '待承保',
            text: '付款成功，请耐心等待保险出单'
          }
        case 3:
          return {
            icon: 'icon-pay',
            status: '待付款',
            text: '订单报价成功，请尽快付款'
          }
        case 4:
          return {
            icon: 'icon-success',
            status: '已承保',
            text: '车险投保成功！'
          }
        case 5:
          return {
            icon: 'icon-faild',
            status: '核保失败',
            text: '您的保单信息未通过审核，请核对信息后重试'
          }
      }
    },
    jump (url) {
      this.$router.push(url)
    },
    getDetail () {
      this.$http({
        method: 'jsonp',
        url: orderInfo,
        jsonp: 'callback',
        jsonpCallback: 'json',
        params: this.form
      })
      .then(res => {
        console.log(res)
        this.order = res.body.data.order
        this.order.createTime = dateFormat(this.order.createTime)
        this.$localStorage.set('orderDetail', JSON.stringify(this.order))
        for (const i in this.order) {
          console.log(this.order[i])
          if (i === 'insurance') {
            this.order[i].forEach(el => {
              if (el.type === '0') {
                this.hasForce = true
              } else {
                this.hasInsurance = true
              }
            })
          }
        }
      })
    }
  }
}



